/* @flow */

var HasBlockList = require('../mixins/has_block_list')
var BlockMenu    = require('./block_menu')
var EditorBlock  = require('./editor_block')
var React        = require('react')

var EditorBlockList = React.createClass({displayName: 'EditorBlockList',

  mixins: [ HasBlockList ],

  blockComponents:function()                       {
    var blockListId = this.blockListId()
    var block = this.props.block

    return this.state.blockIds.map(function(blockId, i) {
      return (
        React.createElement("div", {key: blockId }, 
          React.createElement(EditorBlock, {initialBlockId: blockId }), 
          React.createElement(BlockMenu, {block: block, blockListId: blockListId, position:  i + 1})
        )
      )
    }, this)
  },

  render:function()      {
    return (
      React.createElement("div", {className: "col-blocks"}, 
        React.createElement(BlockMenu, {block:  this.props.block, blockListId:  this.blockListId() }), 
         this.blockComponents() 
      )
    )
  }

})

module.exports = EditorBlockList



